@each $breakpoint in map-keys($grid-breakpoints){
  @include media-breakpoint-up($breakpoint){
    $infix:breakpoint-infix($breakpoint, $grid-breakpoints);

    @each $prop, $abbrev in (margin: m, padding: p){
      @each $size, $length in $spacers{

        .dds__#{$abbrev}#{$infix}-#{$size}{ #{$prop}:$length !important; }
        .dds__#{$abbrev}t#{$infix}-#{$size},
        .dds__#{$abbrev}y#{$infix}-#{$size}{
          #{$prop}-top:$length !important;
        }
        .dds__#{$abbrev}r#{$infix}-#{$size},
        .dds__#{$abbrev}x#{$infix}-#{$size}{
          #{$prop}-right:$length !important;
        }
        .dds__#{$abbrev}b#{$infix}-#{$size},
        .dds__#{$abbrev}y#{$infix}-#{$size}{
          #{$prop}-bottom:$length !important;
        }
        .dds__#{$abbrev}l#{$infix}-#{$size},
        .dds__#{$abbrev}x#{$infix}-#{$size}{
          #{$prop}-left:$length !important;
        }
      }
    }
    .dds__m#{$infix}-auto{ margin:auto !important; }
    .dds__mt#{$infix}-auto,
    .dds__my#{$infix}-auto{
      margin-top:auto !important;
    }
    .dds__mr#{$infix}-auto,
    .dds__mx#{$infix}-auto{
      margin-right:auto !important;
    }
    .dds__mb#{$infix}-auto,
    .dds__my#{$infix}-auto{
      margin-bottom:auto !important;
    }
    .dds__ml#{$infix}-auto,
    .dds__mx#{$infix}-auto{
      margin-left:auto !important;
    }
  }
}
