.dds__carousel {
  position: relative;
  touch-action: pan-y;
}

.dds__carousel-inner {
  overflow: hidden;
  position: relative;
  width: 100%;
}

.dds__carousel-item {
  align-items: center;
  backface-visibility: hidden;
  display: none;
  perspective: 1000px;
  position: relative;
  width: 100%;
}

.dds__carousel-item.dds__active,
.dds__carousel-item-next,
.dds__carousel-item-prev {
  @include transition($carousel-transition);
  display: block;
}

.dds__carousel-item-next,
.dds__carousel-item-prev {
  position: absolute;
  top: 0;
}

.dds__carousel-item-next.dds__carousel-item-left,
.dds__carousel-item-prev.dds__carousel-item-right {
  transform: translateX(0);

  @supports (transform-style: preserve-3d) {
    transform: translate3d(0, 0, 0);
  }
}

.dds__carousel-item-next,
.dds__active.dds__carousel-item-right {
  transform: translateX(100%);

  @supports (transform-style: preserve-3d) {
    transform: translate3d(100%, 0, 0);
  }
}

.dds__carousel-item-prev,
.dds__active.dds__carousel-item-left {
  transform: translateX(-100%);

  @supports (transform-style: preserve-3d) {
    transform: translate3d(-100%, 0, 0);
  }
}


.dds__carousel-control-prev,
.dds__carousel-control-next {
  align-items: center;
  bottom: 0;
  color: $carousel-control-color;
  display: flex;
  justify-content: center;
  opacity: $carousel-control-opacity;
  position: absolute;
  text-align: center;
  top: 0;
  width: $carousel-control-width;
  @include hover-focus {
    color: $carousel-control-color;
    opacity: 0.9;
    outline: 0;
    text-decoration: none;
  }
}

.dds__carousel-control-prev {
  left: 0;
  @if $enable-gradients {
    background: linear-gradient(90deg, rgba($black, 0.25), rgba($black, 0.001));
  }
}

.dds__carousel-control-next {
  right: 0;
  @if $enable-gradients {
    background: linear-gradient(270deg, rgba($black, 0.25), rgba($black, 0.001));
  }
}

.dds__carousel-indicators {

  @include mixin_dds__carousel-indicators;
  bottom: 10px;
  display: flex;
  justify-content: center;
  left: 0;
  list-style: none;
  margin-bottom: -2.8125rem;
  margin-left: $carousel-control-width;
  margin-right: $carousel-control-width;
  padding-left: 0;
  position: absolute;
  right: 0;
  z-index: 15;

  li {
    background-color: rgba($carousel-indicator-active-bg, 0.5);
    cursor: pointer;
    flex: 0 1 auto;
    height: $carousel-indicator-height;
    margin-left: $carousel-indicator-spacer;
    margin-right: $carousel-indicator-spacer;
    position: relative;
    text-indent: -999px;
    width: $carousel-indicator-width;

    &::before {
      content: "";
      display: inline-block;
      height: 10px;
      left: 0;
      position: absolute;
      top: -10px;
      width: 100%;
    }

    &::after {
      bottom: -10px;
      content: "";
      display: inline-block;
      height: 10px;
      left: 0;
      position: absolute;
      width: 100%;
    }
  }

  .dds__active {
    background-color: $carousel-indicator-active-bg;
  }
}

/* CSS detection for Edge */
